import { TabBar } from 'antd-mobile';
import React from 'react';
import GridExp from './gridexp.jsx';
export default class  TabBarExp extends  React.Component{
    constructor(props) {
        super(props);
        this.state = {
            selectedTab: 'redTab',
            hidden: false,
        }
    }
    renderContent(pageText) {
        return (
            <div style={{ backgroundColor: 'white', textAlign: 'center' }}>
                <div style={{ paddingTop: 60 }}>{pageText} tab {pageText}</div>
                <a style={{ display: 'block', marginTop: 40 }} onClick={(e) => {
                 e.preventDefault();
                 this.setState({
                    hidden: !this.state.hidden,
                 });
                }}> tab-bar /</a>
                <GridExp/>
            </div>
        );
    }
    render() {
        return (
            <TabBar
                unselectedTintColor="#949494"
                tintColor="#33A3F4"
                barTintColor="white"
                hidden={this.state.hidden}
            >
                <TabBar.Item
                    title=""
                    key=""
                    style=""
                    icon={require('./images/TabBar_HomeBar_web.png')}
                    selectedIcon={require('./images/TabBar_HomeBar_Sel_web.png')}
                    selected={this.state.selectedTab === 'blueTab'}
                    onPress={() => {
            this.setState({
              selectedTab: 'blueTab',
            });
          }}
                    data-seed="logId"
                >
                    {this.renderContent('')}
                </TabBar.Item>
                <TabBar.Item
                    icon={{ uri: 'https://zos.alipayobjects.com/rmsportal/UNQhIatjpNZHjVf.png' }}
                    selectedIcon={{ uri: 'https://zos.alipayobjects.com/rmsportal/HLkBvJOKnmOfBPO.png' }}
                    title=""
                    key=""
                    badge={1}
                    selected={this.state.selectedTab === 'redTab'}
                    onPress={() => {
            this.setState({
              selectedTab: 'redTab',
            });
          }}
                    data-seed="logId1"
                >
                    {this.renderContent('')}
                </TabBar.Item>
                <TabBar.Item
                    icon={{ uri: 'https://zos.alipayobjects.com/rmsportal/EljxLrJEShWZObW.png' }}
                    selectedIcon={{ uri: 'https://zos.alipayobjects.com/rmsportal/LWNaMdwAFSmYBFw.png' }}
                    title=""
                    key=""
                    selected={this.state.selectedTab === 'greenTab'}
                    onPress={() => {
            this.setState({
              selectedTab: 'greenTab',
            });
          }}
                >
                    {this.renderContent('')}
                </TabBar.Item>
                <TabBar.Item
                    icon={{ uri: 'https://zos.alipayobjects.com/rmsportal/YWpPVCVOnJoCYhs.png' }}
                    selectedIcon={{ uri: 'https://zos.alipayobjects.com/rmsportal/WadBBxOIZtDzsgP.png' }}
                    title=""
                    key=""
                    selected={this.state.selectedTab === 'yellowTab'}
                    onPress={() => {
            this.setState({
              selectedTab: 'yellowTab',
            });
          }}
                >
                    {this.renderContent('')}
                </TabBar.Item>
            </TabBar>
        );
    }
}




